import React from 'react';
import { GameStatus, DifficultyLevel, DIFFICULTY_PRESETS } from '../types';

const DIFFICULTY_LABELS: Record<DifficultyLevel, string> = {
  EASY: '',
  MEDIUM: '',
  HARD: ''
};

interface GameStatusProps {
  status: GameStatus;
  mineCount: number;
  flagCount: number;
  timeElapsed: number;
  onNewGame: () => void;
  onDifficultyChange: (difficulty: DifficultyLevel) => void;
  currentDifficulty: DifficultyLevel;
}

export const GameStatusPanel: React.FC<GameStatusProps> = ({
  status,
  mineCount,
  flagCount,
  timeElapsed,
  onNewGame,
  onDifficultyChange,
  currentDifficulty
}) => {
  const getStatusEmoji = (): string => {
    switch (status) {
      case GameStatus.READY:
        return '';
      case GameStatus.PLAYING:
        return '';
      case GameStatus.WON:
        return '';
      case GameStatus.LOST:
        return '';
      default:
        return '';
    }
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const remainingMines = mineCount - flagCount;

  return (
    <div className="game-status">
      <div className="game-header">
        <div className="game-info">
          <div className="info-item">
            <span className="label">:</span>
            <span className="value">{remainingMines}</span>
          </div>
          
          <button 
            className="new-game-button"
            onClick={onNewGame}
            title=" "
          >
            {getStatusEmoji()}
          </button>
          
          <div className="info-item">
            <span className="label">:</span>
            <span className="value">{formatTime(timeElapsed)}</span>
          </div>
        </div>
        
        <div className="difficulty-selector">
          <label>:</label>
          <select 
            value={currentDifficulty}
            onChange={(e) => onDifficultyChange(e.target.value as DifficultyLevel)}
          >
            {(Object.keys(DIFFICULTY_PRESETS) as DifficultyLevel[]).map((key) => {
              const preset = DIFFICULTY_PRESETS[key];
              return (
                <option key={key} value={key}>
                  {DIFFICULTY_LABELS[key]} ({preset.rows}x{preset.cols}, {preset.mines} )
                </option>
              );
            })}
          </select>
        </div>
      </div>
      
      {status === GameStatus.WON && (
        <div className="game-message victory">
           !  ! 
        </div>
      )}
      
      {status === GameStatus.LOST && (
        <div className="game-message defeat">
            !   ! 
        </div>
      )}
    </div>
  );
};